* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  font-family: sans-serif;
  background: #ccc;
}

.nav-wrapper {
  margin-bottom: 40px;
}

.homeBody {
  width: 1350px;
  margin: 20px auto;
}

nav a.active{
  border-bottom: 5px solid #333;
  -webkit-transition: all ease 0.5s;
  -o-transition: all ease 0.5s;
  transition: all ease 0.5s;
}

.home .post img{
  width: 100px;
  position: absolute;
  top: 20px;
  left: 10px;
  opacity: 0.6;
}
.postImg {
  width: 100%;
}

.home .post{
  overflow: hidden;
  padding-left: 100px;
}

.logoImg {
  width: 50px;
  margin: 7px;
}

/* -------- Log In -------- */

.logInForm {
  padding: 20px 0;
  background: #f1f1f1;
  width: 100%;
  margin: 40px auto;
}

.logInForm label {
  font-size: 14px;
  margin: 3px 0 0 0;
}

.logInForm input {
  margin: 5px 0;
  padding: none;
  line-height: 12px;
} 

.logInForm button {
  display: block;
  padding: 10px 20px;
  background: rgb(161, 49, 49);
  outline: none;
  border: none;
  color: #f1f1f1;
  font-weight: bold;
  font-size: 16px;
  margin: 5px auto 0 auto;
}





.home {
  width: 1300px;
  margin: 100px auto;
}


/* ------leftBar------ */


.leftSideBar {
  width: 245px;
  background-color: #f1f1f1;
  -webkit-box-shadow: 3px 3px #0000001a;
          box-shadow: 3px 3px #0000001a;
  padding-bottom: 40px;
  float: left;
  margin: 0 20px;
}

.leftNav {
  width: 100%;
  width: 245px;
}

.categBtn {
  width: 100%;
  height: 38px;
  padding-left: 25px;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 14px;
  text-align: left;
}

.categBtn:hover {
  background: #fafafa;
}

.arrowSymbol {
  width: 12px;
  float: right;
  margin-right: 20px;
}

.breedCheckBox {
  width: 100%;
  padding: 2px 15px;
  cursor: pointer;
  font-size: 12px;
  background: #fafafa;
}
.breedCheckBox:hover {
  background: rgb(241, 241, 241);
}

.checkMark {
  width: 8px;
  height: 12px;
  border-right: 4px solid #3d3d3d;
  border-bottom: 4px solid #3d3d3d;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}
.checkSquare {
  width: 18px;
  height: 18px;
  padding: 1px 4px;
  border: 1px solid #ccc;
  display: inline-block;
  margin: 0 10px;
}

.leftProfile {
  width: 240px;
  margin: 10px 2px;
  padding: 20px 16px;
}


.leftProfilePic {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  float: left;
  margin-right: 10px;
}

.leftProfileText{
  width: 140px;
  line-height: 12px;
  font-size: 10px;
  display: inline-block;
  color: #3d3d3d;

}
.leftProfileText button {
  padding: 3px 5px;
  color: #3d3d3d;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}
.leftProfileText button:hover{
  background: #ffffff;
}
.leftProfileText button:active{
  background: transparent;
}

.profilePng {
  width: 100%;
}

.leftProfileName{
  font-weight: bold;
  font-size: 13px;
  line-height: 16px;
}



/* ------post------ */

.homeMain {
  width: Calc(100% - 450px);
  display: inline-block;
}
.myPosts {
  width: 100%;
}
.mypost {
  background: white;
  height: 140px;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  margin-bottom: 20px;
}
.postPic {
  width: 250px;
  height: 140px;
  background: #333;
  overflow: hidden;
}

h5 {
  margin: 0 0 8px 0;
}

.postText {
  padding: 10px 20px 30px 20px;
  width: Calc(100% - 300px);
  height: 140px;
  background: #f1f1f1;
  position: relative;
}

.postTitle {
  font-size: 24px;
  font-weight: bold;
}

.postBody {
  font-size: 14px;
  line-height: 16px;
  overflow: auto;
  height: 65px;
  padding-right: 10px;
}

div::-webkit-scrollbar {
  width: 5px;
}

div::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 2px grey;
          box-shadow: inset 0 0 2px grey; 
  border-radius: 3px;
}
 
/* Handle */
div::-webkit-scrollbar-thumb {
  background: #3d3d3d; 
  border-radius: 3px;
}

/* Handle on hover */
div::-webkit-scrollbar-thumb:hover {
  background: rgb(161, 49, 49); 
}

.postPrice {
  width: 50px;
  height: 140px;
  background: rgb(161, 49, 49);
}


.postLocation {
  width: 150px;
  height: 16px;
  position: absolute;
  bottom: 6px;
  left: 20px;
  font-size: 12px;
  line-height: 16px;
  font-weight: bold; 
}

.postLocation img, .postContact img{
  width: 16px;
  float: left;
  margin-right: 5px;
}


.postContact {
  height: 16px;
  position: absolute;
  bottom: 6px;
  right: 20px;
  font-size: 12px;
  line-height: 16px;
  font-weight: bold; 
}

.postPrice h2{
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  margin: 35px auto;
  font-size: 36px;
  color: #fafafa;
  font-weight: bold;
}



/* ------Profile------ */

.profileBody {
  width: 1350px;
  margin: 20px auto;
}


.profileProfilePic {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  -webkit-box-shadow: 3px 3px #0000001a;
          box-shadow: 3px 3px #0000001a;
  overflow: hidden;
  margin: 20px auto;
}
.profileProfilePic img{
  width: 110%;
}
.profileUserinfo{
  width: 100%;
  font-weight: bold;
  text-align: center;
}
.name{
  font-size: 18px;
}
.phone {
  font-size: 14px;
}
.profileMain {
  width: Calc(100% - 350px);
  display: inline-block;
}

.addPostBox {
  width: 100%;
  margin: 0 0 20px 0;
  background: #f1f1f1;
  padding: 10px 50px;
  color: rgb(161, 49, 49);
}

.addPostBox h2, .profilePosts{
  margin: 10px 0 20px 0;
  text-align: center;
}

label {
  display: block;
  font-weight: bold;
  color: #5c5c5c;
  margin: 5px 0;
}

input {
  width: 100%;
  line-height: 20px;
  border: none;
  border-bottom: 1px solid rgb(161, 49, 49);
  background: transparent;
  margin: 5px 0;
  padding: 3px 0;
  outline: none;
}

.addPostBox button {
  display: block;
  padding: 10px 20px;
  background: rgb(161, 49, 49);
  outline: none;
  border: none;
  color: #f1f1f1;
  font-weight: bold;
  font-size: 16px;
  margin: 10px auto;
}

select {
  padding: 5px 10px;
  margin: 5px 180px;
  background: rgb(182, 72, 72);
  color: #f1f1f1;
  border: none;
  outline: none;
}

.backToHome {
  padding: 10px;
  background: green;
  color: #f1f1f1;
  font-weight: bold;
  font-size: 20px;
  border-radius: 50%;
  border: none;
  outline: none;
  margin: 40px auto 0 auto;
  display: block;
}

.leftSideLogo {
  width: 100%;
  padding: 20px 10px;
  background: rgb(182, 72, 72);
  margin: 10px 0;
}

.leftSideLogo img {
  width: 100%;
}


